import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import messagesList from '@/views/pages/messages/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import shangjiaList from '@/views/pages/shangjia/list'
import shangjiaDetail from '@/views/pages/shangjia/formModel'
import shangjiaAdd from '@/views/pages/shangjia/formAdd'
import lingshifenleiList from '@/views/pages/lingshifenlei/list'
import lingshifenleiDetail from '@/views/pages/lingshifenlei/formModel'
import lingshifenleiAdd from '@/views/pages/lingshifenlei/formAdd'
import lingshixinxiList from '@/views/pages/lingshixinxi/list'
import lingshixinxiDetail from '@/views/pages/lingshixinxi/formModel'
import lingshixinxiAdd from '@/views/pages/lingshixinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import cartList from '@/views/pages/shop_order/cart'
import addressList from '@/views/pages/shop_address/list'
import order_confirm from '@/views/pages/shop_order/confirm'
import ordersList from '@/views/pages/shop_order/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'messagesList',
			component: messagesList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'shangjiaList',
			component: shangjiaList
		}, {
			path: 'shangjiaDetail',
			component: shangjiaDetail
		}, {
			path: 'shangjiaAdd',
			component: shangjiaAdd
		}
		, {
			path: 'lingshifenleiList',
			component: lingshifenleiList
		}, {
			path: 'lingshifenleiDetail',
			component: lingshifenleiDetail
		}, {
			path: 'lingshifenleiAdd',
			component: lingshifenleiAdd
		}
		, {
			path: 'lingshixinxiList',
			component: lingshixinxiList
		}, {
			path: 'lingshixinxiDetail',
			component: lingshixinxiDetail
		}, {
			path: 'lingshixinxiAdd',
			component: lingshixinxiAdd
		}
        , {
            path: 'storeupList',
            component: storeupList
        }
		, {
			path: 'cartList',
			component: cartList
		}
		, {
			path: 'addressList',
			component: addressList
		}
		, {
			path: 'order_confirm',
			component: order_confirm
		}
		, {
			path: 'ordersList',
			component: ordersList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
