<script setup>
	import {
		ref,
		getCurrentInstance
	} from 'vue';
	import moment from 'moment'
	import {
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const router = useRouter()
	//零食信息推荐
	const lingshixinxiRecomList = ref([])
	const getlingshixinxiRecomList = () => {
		let autoSortUrl = 'lingshixinxi/autoSort'
		if(context?.$toolUtil.storageGet('frontToken')){
			autoSortUrl = "lingshixinxi/autoSort2"
		}
		let params = {
			sfsh: '是',
			page: 1,
			limit: 10
		}
		context?.$http({
			url: autoSortUrl,
			method: 'get',
			params: params
		}).then(res => {
			lingshixinxiRecomList.value = res.data.data.list
			lingshixinxiRecomList.value.forEach(item=>{
				if(!isHttp(item.tupian)){
					item.imgUrls = item.tupian.split(',').map(item=>context.$config.url+item)
				}
			})
		})
	}
	//零食信息首页展示
	const lingshixinxiHomeList = ref([])
	const getlingshixinxiHomeList = () => {
		let params = {
			page: 1,
			limit: 6
		}
		context?.$http({
			url: 'lingshixinxi/list',
			method: 'get',
			params: params
		}).then(res => {
			lingshixinxiHomeList.value = res.data.data.list
			lingshixinxiHomeList.value.forEach(item=>{
				if(!isHttp(item.tupian)){
					item.imgUrls = item.tupian.split(',').map(item=>context.$config.url+item)
				}
			})
		})
	}
	//新品资讯弹窗
	import formModel from './news/formModel'
	const newsFormModelRef = ref(null)
	//新品资讯
	const newsList = ref([])
	const getNewsList = () => {
		context?.$http({
			url: 'news/list',
			method: 'get',
			params:{
				page:1,
				limit: 4
			}
		}).then(res=>{
			newsList.value = res.data.data.list
			newsList.value.forEach(item=>{
				if(!isHttp(item.picture)){
					item.imgUrl=context.$config.url+item.picture.split(',')[0]
				}
			})
		})
	}
	const newsDetailClick = (item) => {
		if (item && item.id){
			newsFormModelRef.value.init(item.id)
		}
	}
	//判断图片链接是否带http
	const isHttp = (str) => {
        return str && str.substr(0,4)=='http';
    }
	//跳转详情
	const detailClick = (table,id) => {
		router.push(`/index/${table}Detail?id=${id}`)
	}
	const moreClick = (table) => {
		router.push(`/index/${table}List`)
	}
	const init = () => {
		//零食信息推荐
		getlingshixinxiRecomList()
		//零食信息首页展示
		getlingshixinxiHomeList()
		//新品资讯
		getNewsList()
	}
	init()
</script>

